html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
}
.Upload 
{
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.UploadBox
{
    display: block;
    width: 30%;
    margin-top: 50px;
    border: 3px #eee solid;
    padding: 15px;
}

.ListItem
{
    position: relative;
    display: block;
    margin-left: 1%;
    margin-top: 10px;
    float: left;
    width: 24%;
}

.ListItem>.ListName
{
    position: absolute;
    bottom: 0;
    text-align: center;
    height: 15%;
    width: 100%;
    background: rgba(25, 25, 25, 0.5);
    color: white;
}

